import PropTypes from 'prop-types';
import styles from './transactionHistory.module.css';

const TransactionHistory = ({ items }) => (
        <table className={styles.transaction_history}>
            <thead>
                <tr>
                    <th>Type</th>
                    <th>Amount</th>
                    <th>Currency</th>
                </tr>
            </thead>

            <tbody>
            {items.map(item => (
                <tr key={item.id}>
                    <td>{item.type}</td>
                    <td>{item.amount}</td>
                    <td>{item.currency}</td>
                </tr>
            ))}
            </tbody>
        </table>
);

TransactionHistory.propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,       //— уникальный идентификатор транзакции
      type: PropTypes.string.isRequired,     //— тип транзакции
      amount: PropTypes.string.isRequired,   //— сумма транзакции
      currency: PropTypes.string.isRequired, //— тип валюты
    }),
  ),
};

export default TransactionHistory;